import './cell.less';
import { Cell, StickerOpt } from '@fui/sheet/types';
import { CellType } from '../table.types';
import { Transform2CellArgs } from './cell';
import { TableExternalContext } from '../table.cell_generator';
import { cellControllerProvider } from './cell_controllers/cell_controller';
import { CellControllerManager } from '../table.cell_controller_manager';
import { transformPosition2Id } from '../table.service';
import { CellErrorTipManager } from '../table.cell_error_tip_manager';
import { transformCell2Widget } from './cell.widget';
import { CellCacheManager } from '../table.cell_value_manager';
import { Label } from '@fui/core';
import { transformCell2Filter } from './cell.filter';

export abstract class AbstractTableCell {
    protected options: Transform2CellArgs;
    protected external: TableExternalContext;
    protected cellControllerManager: CellControllerManager;
    private cellErrorTipManager: CellErrorTipManager;
    private cellCacheManager: CellCacheManager;
    public id = BI.UUID();
    
    public getCellStickers(): (CellHtml | undefined)[] {
        return [
            this.toHtml(),
            this.toWidget(),
            this.toState(),
            this.toFilter(),
        ];
    }

    public constructor(opts: Transform2CellArgs, external: TableExternalContext) {
        this.options = opts;
        this.external = external;

        this.cellControllerManager = <CellControllerManager | undefined>(this.external.getStickerManagers().get(CellControllerManager.NAME)) || new CellControllerManager();

        this.cellErrorTipManager = <CellErrorTipManager | undefined>(this.external.getStickerManagers().get(CellErrorTipManager.NAME)) || new CellErrorTipManager();

        this.cellCacheManager = <CellCacheManager | undefined>(this.external.getStickerManagers().get(CellCacheManager.NAME)) || new CellCacheManager();

        external.getStickerManagers().set(CellControllerManager.NAME, this.cellControllerManager);

        external.getStickerManagers().set(CellErrorTipManager.NAME, this.cellErrorTipManager);

        external.getStickerManagers().set(CellCacheManager.NAME, this.cellCacheManager);

        this.id = BI.isNotNull(opts.cell.id) ? `${opts.cell.id}` : transformPosition2Id(opts.cell.position.x, opts.cell.position.y);
    }

    protected getTextByCellValue(value?: string | object) {
        const { cell, widget } = this.options;

        const text = cellControllerProvider.getTextTransformer(widget?.type)(AbstractTableCell.transform2CellText(cell.text || value || cell.value));

        return BI.isNull(cell.cv) || BI.isNull(cell.transformed) ? text : BI.contentFormat(text, cell.fmt);
    }

    protected abstract toFullCell(): CellType;

    protected isDirectWidget() {
        const { cell, widget } = this.options;

        return !!((cell.isShowWidgets || cellControllerProvider.isDirect(widget?.type)) && widget);
    }

    public toCell(): CellType {
        return this.isDirectWidget()
            ? this.toEmptyCell()
            : this.toFullCell();
    }

    protected toHtml(): CellHtml | undefined {
        return undefined;
    }

    public static transform2CellText(text?: string | object): string {
        return BI.isObject(text)
            ? JSON.stringify(text)
            : BI.isNull(text)
                ? ''
                : `${text}`;
    }

    public static transform2String(v: any): string {
        return `${BI.isNull(v) ? '' : v}`;
    }

    protected fireEvent(eventName: string, args: { e: Event, x: number, y: number, cell: Cell, [key: string]: any }): Promise<any>[] {
        const promises: Promise<any>[] = [];

        this.options.listeners
            .forEach(listener => {
                if (listener.eventName === eventName && listener.action) {
                    promises.push(Promise.resolve<any>(listener.action(args)));
                }
            });

        return promises;
    }

    private toEmptyCell() {
        const { cell, celladvancedattr, style } = this.options;

        const { borderStyle, backgroundStyle } = style;
    
        return {
            type: BI.AbsoluteLayout.xtype,
            listeners: [
                {
                    eventName: 'CELL_DOUBLE_CLICK',
                    action: (e: Event, x: number, y: number, cell: Cell) => {
                        this.fireEvent('CELL_DOUBLE_CLICK', { e, x, y, cell, hyperLinks: [], id: this.id });
                        
                        return true;
                    },
                },
                {
                    eventName: 'CELL_CLICK',
                    action: (e: Event, x: number, y: number, cell: Cell) => {
                        this.fireEvent('CELL_CLICK', { e, x, y, cell, hyperLinks: [], id: this.id });
                        
                        return true;
                    },
                },
                {
                    eventName: 'CELL_MOUSE_LEAVE',
                    action: (e: Event, x: number, y: number, cell: Cell) => {
                        this.fireEvent('CELL_MOUSE_LEAVE', { e, x, y, cell });
                    },
                },
                {
                    eventName: 'CELL_MOUSE_DOWN',
                    action: (e: Event, x: number, y: number, cell: Cell) => {
                        this.fireEvent('CELL_MOUSE_DOWN', { e, x, y, cell });
                    },
                },
                {
                    eventName: 'CELL_HOVER',
                    action: (e: Event, x: number, y: number, cell: Cell) => {
                        this.fireEvent('CELL_HOVER', { e, x, y, cell });
                    },
                },
            ],
            borderStyle,
            backgroundStyle,
            canCopy: true,
            cursor: undefined,
            valid: cell.valid,
            title: celladvancedattr?.tooltipText,
            position: cell.position,
        };
    }

    protected toWidget(): CellHtml | undefined {
        return transformCell2Widget.apply(this);
    }

    /**
     * 创建状态标记
     */
    protected toState(): CellHtml | undefined {
        if (this.options.widget) {
            return undefined;
        }

        const { edited, errorMsg } = this.options.cell;

        if (!edited && !errorMsg) {
            return undefined;
        }

        return {
            id: `${this.id}_state`,
            init: (e, width, height) => {
                const wraffleWidth = width - 1;
                const wraffleHeight = height - 1;

                const items = this.getStateWidgetJson(wraffleWidth, wraffleHeight);

                if (BI.size(items) > 0) {
                    e.css('overflow', 'visible');

                    BI.createWidget({
                        type: BI.DefaultLayout.xtype,
                        element: e.el,
                        items: [{
                            type: BI.AbsoluteLayout.xtype,
                            width: wraffleWidth,
                            height: wraffleHeight,
                            items,
                        }],
                    });
                }
            },
            value: '',
        };
    }

    public getStateWidgetJson(wraffleWidth: number, _wraffleHeight: number) {
        const { edited, errorMsg } = this.options.cell;

        const cellCls = this;

        const result: {[key: string]: any}[] = [];

        if (edited) {
            result.push({
                el: {
                    type: BI.Icon.xtype,
                    height: 10,
                    width: 10,
                    cls: 'cell-edited-tip',
                },
                top: 0,
                left: 0,
            });
        }

        if (errorMsg) {
            result.push({
                el: {
                    type: BI.Icon.xtype,
                    height: 15,
                    width: 15,
                    cls: 'cell-warning-tip',
                    title: () => errorMsg,
                },
                left: 0,
                bottom: 0,
            }, {
                el: {
                    type: BI.Label.xtype,
                    invisible: true,
                    cls: 'report-cell-error-tip',
                    hgap: 5,
                    vgap: 5,
                    text: errorMsg,
                    created(this: Label) {
                        cellCls.cellErrorTipManager.register(this, cellCls.id);
                    },
                },
                left: wraffleWidth + 1,
                top: 1,
            });
        }

        return result;
    }

    protected toFilter(): CellHtml | undefined {
        return transformCell2Filter.apply(this);
    }
}

export interface CellHtml {
    value: string;
    id: StickerOpt['id'];
    init?: StickerOpt['init'];
    cursor?: string;
    style?: {
        [key: string]: number | string
    };

    /**
     * 需要富文本拓展
     */
    extended?: boolean;
    lazy?: StickerOpt['lazy'];
}

export type EventCellWidgetChangeArgs = Parameters<AbstractTableCell['fireEvent']>[1] & {
    id: string;
    value: any;
    direct: boolean;
    callback?: Function;
}
